#items {
  overflow: hidden;
  padding: 1.9rem 0 1.2rem;
  max-width: 7.5rem;
  margin: auto;
}
#items li {
  float: left;
  width: 3.72rem;
  background: #fff;
  margin-bottom: 0.06rem;
}
#items.active li {
  width: 7.5rem;
  padding: 0.2rem 0;
  margin-bottom: 0;
  border-bottom: 1px solid #f4f5f9;
}
#items li:nth-of-type(2n) {
  float: right;
}
#items li img {
  display: block;
  width: 3.5rem;
  height: 3.5rem;
  margin: 0 auto;
}
#items.active li img {
  float: left;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 0.1rem;
}
