  float: left;
  width: 4.5rem;
}
#items li .title {
  color: #333;
  font-size: 0.26rem;
  padding: 0.25rem 0 0 0.15rem;
  overflow: hidden;
  line-height: 0.3rem;
  text-overflow: ellipsis;
  white-space: nowrap;
}
#items.active li .title {
  font-size: 0.3rem;
  line-height: 0.4rem;
  height: 0.8rem;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  display: box;
  -webkit-line-clamp: 2;
  white-space: normal;
  -webkit-box-orient: vertical;
  margin-bottom: 0.3rem;
}
#items li .price {
  color: #e30b22;
  display: block;
  padding: 0.2rem 0 0.2rem 0.15rem;
}
#items.active li .price {
  font-size: 0.34rem;
}
#items li .praise {
  color: #85868a;
